:root {
  --text-primary: #1a1a1a;
  --text-secondary: #666;
  --bg-primary: #fff;
  --bg-secondary: #f5f5f7;
}

.dark {
  --text-primary: #f5f5f7;
  --text-secondary: #d2d2d7;
  --bg-primary: #1a1a1a;
  --bg-secondary: #2c2c2e;
}

html {
  scroll-behavior: smooth;
}
::selection {
  background: #0071e3;
  color: #fff;
}
/* 添加滚动条主题支持 */
::-webkit-scrollbar {
  width: 8px;
  background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--text-secondary);
  border-radius: 4px;
}

.prose {
  max-width: 65ch;
  line-height: 1.8;
  font-size: 1.1rem;
  color: var(--text-primary);
}

.prose h1, .prose h2, .prose h3 {
  color: var(--text-primary);
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.prose p {
  margin-bottom: 1.25rem;
}

.prose code {
  font-family: 'Fira Code', monospace;
}